/* end palette vars */
:export {
  _breakPointHigh: 1280;
  _breakPointMediumHigh: 980;
  _breakPointMediumLow: 680;
  _breakPointLow: 400;
  breakPointHigh: 1280px;
  breakPointMediumHigh: 980px;
  breakPointMediumLow: 680px;
  breakPointLow: 400px;
  all: [1280, 980, 680, 400]; }

#register .links {
  margin-bottom: 80px;
  flex-wrap: wrap; }

#register .form .mdc-text-field {
  width: 100%; }

#register .content {
  align-items: center; }

#register .form-label {
  margin-bottom: 30px; }
  #register .form-label .big {
    font-size: 27px; }
  #register .form-label .small {
    font-size: 12px; }

#register a {
  color: #546e7a; }
