.building-bg {
  height: 100%;
  min-height: 700px;
  background: url(../../assets/building_800.png) transparent no-repeat 0% 0%; }

.floors {
  left: 280px;
  top: 110px; }
  .floors .floor {
    margin-top: 14.5px;
    color: #fff;
    text-shadow: 0 0 4px #f00;
    visibility: hidden;
    opacity: 0;
    transition: opacity 500ms ease-out, visibility 500ms; }
    .floors .floor .hole {
      display: inline-block;
      width: 18px;
      height: 25px;
      background-color: #222;
      border: 2px solid #fff;
      /*box-shadow: 0 0 10px 2px #f00;*/
      margin: -8px 10px -7px;
      transform: skewY(-20deg);
      position: absolute; }
    .floors .floor .indicator {
      display: inline-block;
      width: 18px;
      height: 25px;
      background-color: #374b7f;
      border: 2px solid #fff;
      /*background-color: rgba(255,255,255,.3);*/
      box-shadow: 0 0 10px 2px #f00;
      margin: -8px 10px -7px;
      position: relative;
      transform: skewY(-20deg);
      transition: transform 350ms ease-in, margin 350ms ease-in; }
    .floors .floor .indicator::before {
      content: '';
      display: block;
      position: absolute;
      left: 1px;
      top: 10px;
      width: 2px;
      height: 7px;
      background-color: #fff; }
    .floors .floor .indicator::after {
      content: '';
      display: block;
      position: absolute;
      left: 4px;
      top: 5px;
      width: 2px;
      height: 12px;
      background-color: #fff; }
  .floors .floor.active {
    visibility: visible;
    opacity: 1;
    transition: opacity 500ms ease-out; }
    .floors .floor.active .indicator {
      margin: -2px 10px -13px;
      transform: skewY(22deg);
      transition: transform 500ms ease-out, margin 500ms ease-out;
      transition-delay: 200ms; }

.control-panel {
  width: 300px;
  position: absolute;
  top: 60%;
  right: 16px;
  bottom: 16px;
  background-color: rgba(0, 0, 0, 0.3);
  border: 8px solid rgba(0, 0, 0, 0.3); }
