/* all palette vars from mdc theme */
/*
$mdc-theme-primary
$mdc-theme-on-primary
$mdc-theme-secondary
$mdc-theme-on-secondary
$mdc-theme-background
$mdc-theme-on-background
$mdc-theme-surface
$mdc-theme-on-surface
$mdc-theme-error
$mdc-theme-on-error
*/
/* end palette vars */
:export {
  _breakPointHigh: 1280;
  _breakPointMediumHigh: 980;
  _breakPointMediumLow: 680;
  _breakPointLow: 400;
  breakPointHigh: 1280px;
  breakPointMediumHigh: 980px;
  breakPointMediumLow: 680px;
  breakPointLow: 400px;
  all: [1280, 980, 680, 400]; }

.loader-backdrop {
  display: none; }

.loader-backdrop-show {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0; }
  .loader-backdrop-show .loader-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2); }
    .loader-backdrop-show .loader-wrapper .loader-progress {
      display: none; }
    .loader-backdrop-show .loader-wrapper .loader-progress-show, .loader-backdrop-show .loader-wrapper .loader-indicator {
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -50px;
      margin-top: -50px;
      width: 100px;
      height: 100px; }
    .loader-backdrop-show .loader-wrapper .loader-progress-show {
      text-align: center;
      font-size: 25px;
      margin-top: -16.66667px; }
    .loader-backdrop-show .loader-wrapper .loader-indicator {
      color: rgba(0, 0, 0, 0.2);
      animation: spin 1s ease-in-out infinite;
      -webkit-animation: spin 1s ease-in-out infinite; }
      .loader-backdrop-show .loader-wrapper .loader-indicator i {
        font-size: 100px;
        line-height: 100px;
        width: 100%;
        height: 100%;
        text-align: center; }

.loader-backdrop-show.loader-locale {
  position: absolute; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
