/* all palette vars from mdc theme */
/*
$mdc-theme-primary
$mdc-theme-on-primary
$mdc-theme-secondary
$mdc-theme-on-secondary
$mdc-theme-background
$mdc-theme-on-background
$mdc-theme-surface
$mdc-theme-on-surface
$mdc-theme-error
$mdc-theme-on-error
*/
/* end palette vars */
:export {
  _breakPointHigh: 1280;
  _breakPointMediumHigh: 980;
  _breakPointMediumLow: 680;
  _breakPointLow: 400;
  breakPointHigh: 1280px;
  breakPointMediumHigh: 980px;
  breakPointMediumLow: 680px;
  breakPointLow: 400px;
  all: [1280, 980, 680, 400]; }

.modal-set .mdc-dialog {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0; }
  .modal-set .mdc-dialog .mdc-dialog__container {
    transform: none;
    position: fixed;
    top: 5%;
    bottom: 0; }
    .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface {
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      max-width: none;
      max-height: none;
      display: block;
      width: 650px;
      height: 100%;
      position: relative; }
      .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .mdc-dialog__content {
        padding: 0 15px 5px 15px;
        overflow-y: auto;
        position: relative;
        height: 80%; }
      .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header {
        padding: 0;
        border-bottom: 2px solid #fbc02d; }
        .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header .grid-row.dialog-main-row {
          grid-template-columns: auto 50px;
          grid-template-rows: 80px;
          justify-content: stretch;
          padding-left: 15px;
          padding-right: 15px; }
          .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header .grid-row.dialog-main-row .grid-cell {
            align-self: center; }
          .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header .grid-row.dialog-main-row .material-icons {
            cursor: pointer; }
          .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header .grid-row.dialog-main-row .dialog-title {
            color: #546e7a;
            font-size: 26px; }
          .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header .grid-row.dialog-main-row .dialog-description {
            color: #fbc02d; }
          .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header .grid-row.dialog-main-row .dialog-close {
            color: #fbc02d;
            line-height: 0px;
            justify-self: end;
            align-self: center; }
      .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .dialog-header::before {
        display: none; }
      .modal-set .mdc-dialog .mdc-dialog__container .mdc-dialog__surface .mdc-dialog__actions {
        position: absolute;
        width: 100%;
        bottom: 0; }

.modal-set .mdc-dialog.offset1 {
  z-index: 9; }
  .modal-set .mdc-dialog.offset1 .mdc-dialog__container {
    top: 10%; }

